/**
 *  `paper.Jason` 
 * @param {string} paper -  `paper.Jason`
 */
module.exports = function (paper) {
  return paper.txlist.map(problem => {
    switch (problem.cjq_no /*  */) {
      case 'collector.read':
        return {
          type: 'read',
          content: [
            {
              ask: '',
              answer: [
                problem.stlist[0].symbol || problem.stlist[0].value
              ]
            }
          ]
        }
      case 'collector.role':
        return {
          type: 'answer',
          content: problem.stlist[0].question.map(question => ({
            ask: question.ask,
            answer: question.std.map(x => x.value)
          }))
        }
      case 'collector.picture':
        return {
          type: 'info',
          content: [
            {
              ask: problem.stlist[0].value,
              answer: problem.stlist[0].std.map(x => x.value)
            }
          ]
        }
      default:
        return {
          type: 'unsupported',
          content: [
            {
              ask: '',
              answer: [
                'ugh.',
                'squeezr ',
                '',
                ' squeezr ',
                ' Issues ',
                '<3',
                `Type: ${problem.cjq_no}`
              ]
            }
          ]
        }
    }
  })
}
